.post-type-archive-location {

    .location-arch-lower {
        border-top: $base-border;
        clear: both;
        display: block;
        margin-top: em(45px);
        padding-top: em(45px);
    }

}

.locationBlock {
    margin-top: $base-spacing;

    &:first-of-type {
        margin-top: 0;
    }

    @include media( $medium-screen-up ) {
        @include span-columns(6);
        @include omega-reset(3n);
        @include omega(2n);

        &:nth-child(1),
        &:nth-child(2), {
            margin-top: 0;
        }
    }

    @include media( $large-screen-up ) {
        @include span-columns(4);
        @include omega-reset(2n);
        @include omega(3n);

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3), {
            margin-top: 0;
        }
    }
}

.locationImage {
    @include bg();
    height: 250px;
    margin-bottom: 1.5em;
    display: block;
}

.locationBlockTitle {
    font-size: rem(26px);
    font-weight: bold;
    text-transform: none;
    line-height: 1;
    margin-bottom: rem(15px);

    a {
        color: #222;

        &:hover {
            color: $primary-color;
        }
    }
}

.locationBlockLink {
    display: block;
    font-size: rem(14px);
    font-weight: bold;
    margin-top: rem(10px);
    text-transform: uppercase;
}