.gallery-section {
	text-align: center;

	.wrap {
		// max-width: 1440px;
	}

	.section-title {

		&:after {
            left: 50%;
            @include transform(translateX(-50%));
        }
	}

    .gallery-img-wrap {
        @include bg();
        display: block;
        @include span-columns(12);
        position: relative;
        overflow: hidden;

        .gallery-img {
	    	width: 100%;
	        height: 100%;
	        position: absolute;
	        @include bg();
	        transition: all $long-duration $base-timing;

	        &:before {
	            content: "";
	            width: 100%;
	            height: 100%;
	            background: $primary-color;
	            position: absolute;
	            top: 0;
	            left: 0;
	            opacity: 0;
	            transition: all $base-duration $base-timing;
	            z-index: 5;
	        }
	    }

        &:hover {

	        .gallery-img {
	            transform: scale(1.07);

	            &:before {
	                opacity: 0.6;
	            }
	        }
	    }

        .gallery-caption {
        	background: #fff;
        	padding: 0.5em 0.7em;
        	color: #666;
        	font-weight: 600;
        	font-size: 9px;
        	position: absolute;
        	bottom: 5%;
        	left: 5%;
        	max-width: 90%;
        	text-align: left;
        }
    }

	.single-gallery-grid.four-images {

		.gallery-row {
			overflow: hidden;
			display: flex;
            justify-content: center;
            flex-wrap: wrap;

			&:first-child {
				margin-top: -1%;
			}
		}

		.gallery-img-wrap {
	        height: 300px;
	        margin-top: 1.5%;
	        // @include omega-reset(4n);

	        @include media( $medium-screen-up ) {
		        // @include span-columns(6);
		        // @include omega-reset(4n);
	         	// @include omega(2n);
	         	float: left;
			    display: block;
			    margin-right: 1.5%;
			    width: 49%;

			    &:nth-child(2n) {
			    	margin-right: 0;
			    }
        	}

	        @include media( $large-screen-up ) {
	        	margin-top: 1%;

	        	&:nth-child(2n) {
			    	margin-right: 1%;
			    }

	            &:nth-child(2),
	            &:nth-child(5),
	            &:nth-child(12),
	            &:nth-child(14) {
	                // @include span-columns(3);
	                float: left;
				    display: block;
				    margin-right: 1%;
				    width: 25%;
	            }

	            &:nth-child(4),
	            &:nth-child(6),
	            &:nth-child(11),
	            &:nth-child(13) {
	                // @include span-columns(5);
	                float: left;
				    display: block;
				    margin-right: 1%;
				    width: 42%;
	            }
	            // @include span-columns(2);
	            float: left;
			    display: block;
			    margin-right: 1%;
			    width: 15%;
	            // @include omega-reset(2n);
	            @include omega(4n);
	        }
	    }
    }

	.single-gallery-grid.three-images {

		.gallery-row {
			overflow: hidden;
			display: flex;
            justify-content: center;
            flex-wrap: wrap;

			&:first-child {
				margin-top: -1%;
			}
		}

		.gallery-img-wrap {
        	height: 400px;
        	margin-top: 2%;
        	position: relative;
        	// @include omega-reset(3n);

        	@include media( $medium-screen-up ) {
		        // @include span-columns(6);
		        // @include omega-reset(4n);
	         	// @include omega(2n);
	         	float: left;
			    display: block;
			    margin-right: 2%;
			    margin-top: 2%;
			    width: 49%;

			    &:nth-child(2),
			    &:nth-child(5),
			    &:nth-child(8) {
			    	margin-right: 0;
			    }

			    &:nth-child(3n+3) {
			    	margin-right: 0;
			    	width: 100%;
			    }
        	}

	        @include media( $large-screen-up ) {
	        	margin-top: 1%;

	        	&:nth-child(3n+3) {
			    	margin-right: 1%;
			    	width: 32%;
			    }

	        	&:nth-child(8) {
	        		margin-right: 1%;
	        	}

	            &:nth-child(2),
	            &:nth-child(4),
	            &:nth-child(9) {
	                // @include span-columns(5);
	                float: left;
				    display: block;
				    margin-right: 1%;
				    width: 43%;
	            }

	            &:nth-child(3),
	            &:nth-child(5),
	            &:nth-child(7) {
	                // @include span-columns(3);
	                float: left;
				    display: block;
				    margin-right: 1%;
				    width: 23%;
	            }
	            // @include span-columns(4);
	            float: left;
			    display: block;
			    margin-right: 1%;
			    width: 32%;
	            // @include omega-reset(2n);
	            @include omega(3n);
	        }
	    }
    }

    .gallery-button {
    	margin-top: 2.5em;
    }
}